import { createElement } from "travrs";
import PerfectScrollbar from "perfect-scrollbar";

// Styles.
import "perfect-scrollbar/css/perfect-scrollbar.css";
import "./styles.scss";

// Handles all pi-stacja modals.
export default function Modals() {
  let currentModal;
  const listeners = [];
  const root = document.querySelector("div.pie-modal-root") || createElement("div.pie-modal-root");

  document.body.appendChild(root);

  // Get all modals from the page.
  const modals = Array.from(document.querySelectorAll("*[data-pie-modal]"))
    .reduce((acc, modal) => {
      acc[modal.dataset.pieModal] = modal;
      const scrollContent = modal.querySelector(".scroll-content");

      if (scrollContent) {
        new PerfectScrollbar(scrollContent);
      }
      root.appendChild(modal);
      return acc;
    }, {});

  document.addEventListener("click", event => {
    // Hide.
    if (!event.target.dataset.pieModalTrigger) {
      if (event.target === root || event.target.dataset.pieModalClose) {
        root.classList.remove("root-open");
        if (currentModal) {
          currentModal.classList.remove("modal-open");
          setTimeout(() => {
            listeners.forEach(listener =>
              listener(currentModal.dataset.pieModal, "hide", currentModal)
            );
          }, 350);
        }
      }
      return;
    }

    // Select & show.
    currentModal = modals[event.target.dataset.pieModalTrigger];
    if (currentModal) {
      currentModal.classList.add("modal-open");
      root.classList.add("root-open");
      listeners.forEach(listener =>
        listener(currentModal.dataset.pieModal, "show", currentModal)
      );
    }
  });

  return {
    listen: callback => {
      !listeners.includes(callback) && listeners.push(callback);
      return function cleanup() {
        const index = listeners.indexOf(callback);
        listeners.splice(index, 1);
      };
    },
  };
}

// Allows for remote controll of targeted modal from JS code.
export function remoteModal(modalName) {
  const $ = document.querySelector.bind(document);
  const selector = `button.remote-pie-modal-btn[data-pie-modal-trigger="${modalName}"]`;
  const trigger = $(selector) || createElement(selector);
  const modal = $(`*[data-pie-modal="${modalName}"]`);
  const root = $("div.pie-modal-root");

  if (!root || !modal) return;

  document.body.appendChild(trigger);
  return {
    on: () => trigger.click(),
    off: () => root.click(),
    modal,
  };
}
