// Modules.
import { template } from "travrs";
import { remoteModal } from "pistacja/modals";

export default function usppModal(domian) {
  const requertConfig = {
    mode: "cors",
    method: "post",
    cache: "default",
  };

  const modalController = remoteModal("uspp");

  // No modal to display content.
  if (!modalController || !modalController.modal) return;

  const [content, refs] = template(`
    div.uspp-wrapper.stack
      @content::div.uspp-content       
      @link::a.bubble-button.green.KGSolid.--space-i > "WSZYSTKIE ZASOBY DLA TEGO WYMAGANIA"        
  `);

  modalController.modal.appendChild(content);

  document.addEventListener("click", event => {
    if (!event.target.matches("button[data-uspp-id]")) return;

    refs.link.href = `${domian}wyniki-wyszukiwania?q=${event.target.innerText}`;

    // Show modal.
    modalController.on();

    // Display waiting msg. after 400ms.
    const timer = setTimeout(() => {
      refs.content.textContent = "Trwa ładowanie danych...";
    }, 400);

    fetch(`${domian}cli/getpppname.php?code=${event.target.dataset.usppId}`, requertConfig)
      .then(r => r.json())
      .then(({ level, name, subject, section }) => {
        clearTimeout(timer);
        refs.content.innerHTML = `
          <div class="stack --medium">
            <h3 class="KGSolid text-green font-md rail --h-spread --v-start">
              <span>${event.target.innerText}</span>
              <button class="ghost" data-pie-modal-close="true">
                <svg class="icon sm">
                  <use href="${domian}/templates/pistacja/images/icons/pie-icons-sprite.svg#ui-close">
                </svg>
              </button>
            </h3>

            <div class="text-gray">${name}</div>
          
            <hr/>
            <div>
              <div>Przedmiot</div>
              <strong class="text-green">${subject}</strong>
            </div>
            <div>
              <div>Poziom edukacyjny</div>
              <strong class="text-green">${level}</strong>
            </div>
            <div>
              <div>Dział</div>
              <strong class="text-green">${section}</strong>
            </div>
          </div>
        `;
        // Redner MathML.
        window.MathJax && window.MathJax.typeset();
      })
      .catch(err => {
        console.error(err);
        clearTimeout(timer);
        refs.content.textContent = "Błąd danych USPP; Przepraszamy.";
      });
  });
}
